@import '~nav-frontend-skjema-style/src/input.less';

.focusOutline() {
    outline: none;
    box-shadow: 0 0 0 3px @fokusFarge;
    border-radius: 3px;
}
.focusOutline--disabled() {
    outline: none;
    box-shadow: 0 0 0 1px @fokusFarge;
    border-radius: 2px;
}

.hoverOutline() {
    outline: none;
    box-shadow: 0 0 0 2px @fokusFarge;
}

.mixin-transition(@varighet: 150ms, @type: all) {
    transition: @type @varighet cubic-bezier(0.465, 0.183, 0.153, 0.946);
}

.inputStyling() {
    .skjemaelement__input();
}
